@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

*{
  margin: 0px;
  padding: 0px;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root{
  --color-bg : #FFFF;
   --color-bg-varient : rgb(72, 4, 139); 
  --color-primary : rgba(0, 0, 0, 0.751);
  --color-primary-varient : rgba(0, 0, 0, 0.751);
  --color-white : rgba(0, 0, 0, 0.751);
  --color-light : rgba(128, 0, 128, 0.889);

  /* --color-bg : #FAED26;
   --color-bg-varient : #30083f; 
  --color-primary : #5A5560;
  --color-primary-varient : #9D8D8F;
  --color-white : rebeccapurple;
  --color-light : #9B786F; */

  --transition: all 400ms ease;

  --container-width-lg : 75%;
  --container-width-md : 86%;
  --container-width-ms : 90%;
}

html {
  scroll-behavior:  smooth;
}

::-webkit-scrollbar{
  display: none;
}

body{
  font-family: 'Poppins', sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  /* line-height: 1.7; */
  /* background-image: url(../src/Resources/watercolor-4117017.png) cover ; */
  /* background-repeat: no-repeat; */
  /* background-position: center; */
  /* background-size: cover; */
  /* width: 500px;
  height: 100vh; */
}

/* before this line there was some general styles  */

.conatiner{
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,h2,h3,h4,h5{
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
  /* height: 100vh; */
}

section > h2 , section > h5{
  text-align: center;
  color: var(--color-light);
}

section > h2{
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light{
  color: var(--color-light);
}

a {
  color:  var(--color-primary);
  transition: var(--transition);
}

a :hover{
  color : var(--color-white)
}

.btn{
  width:  max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.7rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition:  var(--transition);
}

.btn:hover{
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary{
  background:var(--color-primary);
  color: var(--color-bg);
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}


/* media queries for (medius devices)  */

@media screen and (max-width : 1024px){
  .conatiner{
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}
/* height: 100vh; */

/* media queries for (medius devices)  */

@media screen and (max-width : 600px){
  .conatiner{
    width: var(--container-width-ms);
  }

  section > h2 {
    margin-top: 1rem;
  }
}


